@media screen and (max-width : 1200px) and (min-width: 992px)
{
  .myPhotos, .feed
  {
    font-size: 40px!important;
  }

  #burst-8 {
    max-width: 15%!important;
  }
}

@media screen and (max-width : 992px)
{
  .myPhotos, .feed
  {
    font-size: 30px!important;
  }

#burst-8:after, #burst-8:before {
    height: 100px!important;
    width: 100px!important;
    font-size: 70px!important;
  }
  
  #burst-8 {
    height: 100px!important;
    min-width: 106px!important;
    font-size: 70px!important;
  }
}
@media screen and (max-width : 767px)
{
  .myPhotos
  {
    margin-top: 15px;
    font-size: 25px!important;
  }
  #burst-8 {
    top:0px!important;
  }
  .login, .feed {
    text-align: center;
    margin-bottom: 15px;
  }
}

@media screen and (max-width : 460px)
{
    #burst-8 {
    min-width: 100px;
  }
}

body {
    background-image: url(/pocketdru/pocketPhotoEditor/static/media/Header_BG1.1b3688d8.png);
}

.navbar {
    background-image: url(/pocketdru/pocketPhotoEditor/static/media/Header_BG1.1b3688d8.png);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
    background-color: red;
}

 hr {
    border-top: #d1bf92 dashed 1px;
    border-bottom:#696666 dashed 1px;
    box-shadow: 0 10px 10px -10px #8c8c8c inset;
}

.navLink {
  font-family: 'Paytone One', sans-serif;
    font-size: 54px;
    font-weight: 400;
    text-transform: uppercase;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
    color: #f9ebd9;
}

#burst-8 {
    z-index: 1;
    flex: initial;
    /* width: 130px; */
    max-width: 12.666667%;
    height: 130px;
    position: relative;
    top:20px;
    text-align: center;
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
    border: 3px gray solid;
    border-radius: 10px;
    background: url(/pocketdru/pocketPhotoEditor/static/media/Contact_section_BG.166a955f.png);
  }

  #burst-8:before, #burst-8:after {
    border-radius: 10px;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 130px;
    width: 130px;
    -webkit-transform: rotate(125deg);
            transform: rotate(125deg);
    background: url(/pocketdru/pocketPhotoEditor/static/media/Contact_section_BG.166a955f.png);
  }

  #burst-8:before {
    border-top: 3px gray solid;
    width: 130px;
    height: 130px;
    border-bottom: 3px gray solid;
  }

  #burst-8:after {
    font-family: 'Bungee Inline', cursive;  
    text-shadow: 2px 3px 0px rgba(0,0,0,0.15), 2px 2px 0px #f9ebd9;  
    vertical-align: middle;
    text-align: center;
    color: #f6edd8;
    font-size: 90px;
    letter-spacing: 0.94px;
    content: "PE";
    width: 130px;
    height: 130px;
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
  }
  .pe {
    text-align: center;
    color: #f6edd8;
    font-size: 188px;
    letter-spacing: 0.94px;
  }
 .navbar a:not([href]):not([tabindex]) {
    color: #f9ebd9;
  }

  .myPhotos {
    text-align: center;
  }

  .login {
      z-index: 1;
      font-size: 18px;
  }

  .hrUp {
    position: relative;
    top: -50px;
  }
@media screen and (max-width : 992px) {
    .mainRec {
        max-width: 73%!important;
    }

    .ribbon-text {
        font-size: 1.5em!important;
    }
}

@media screen and (max-width : 767px) {
    .dot {
        margin: 15px auto!important;
    }
}

@media screen and (max-width : 492px) {
    .mainRec {
        height: auto!important;
    }

    .rightRec, .sideRec {
        width: 45px!important;
    }

    .ribbon-text {
        font-size: 1em!important;
    }

    .carousel {
        border: 10px solid #fbfaf6!important;
    }

}

@media screen and (max-width : 365px) {
    .rightRec, .sideRec {
        width: 35px!important;
    }

    .appName {
        font-size: 45px!important;
    }
}

.appName {
    text-shadow: 1px 1px 2px rgba(232, 244, 244, 0.3);
    color: #f9ebd9;
    font-family: 'Bungee Inline', cursive;  
    text-shadow: 2px 3px 0px rgba(0,0,0,0.15), 2px 2px 0px #f9ebd9;
    font-size: 65px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 1.3px;
    text-align: center;
}

.dot {
    height: 10px;
    width: 10px;
    background-color: #fff1ce;
    border-radius: 50%;
    display: inline-block;
    margin: 14px 4px;
  }

.ribbon {
    text-align: center;
    font-family: 'Paytone One', sans-serif;
}

.ribbon .mainRec {
    height: 60px;
    z-index: 1;
    box-shadow: 0px 11px 8px rgba(49, 49, 49, 0.5);
}

.sideRec {
    height: 50px;
    width: 60px;
    position: relative;
    left: 15px;
    top: 12px;
}

.rightRec {
    height: 50px;
    width: 60px;
    position: relative;
    right: 15px;
    top: 12px;
}

.sideRec, .rightRec {
    box-shadow: 0px 3px 0px rgba(49, 49, 49, 0.14);
}

.ribbon-text {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.8);
    color: #4b4e4c;
    /* font-size: 34px; */
    font-weight: 400;
}

.carousel {
    border: 20px solid #fbfaf6;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3);
}
